.vaccine-home-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.vaccine-home-page .swiper-container {
  position: relative;
  width: 7.5rem;
  height: 100%;
}

.vaccine-home-page .swiper-container .item {
  position: relative;
  width: 7.5rem;
  height: 100%;
}

.vaccine-home-page .swiper-container .item img {
  position: relative;
  width: 7.5rem;
  height: 100%;
}

.vaccine-home-page .transparent-btn {
  background-color: rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  height: 0.6rem;
  line-height: 0.6rem;
  color: #fff;
  border-radius: 0.3rem;
  text-align: center;
  cursor: pointer;
  padding: 0 0.2rem;
  min-width: 2rem;
  position: absolute;
  top: 0.6rem;
  right: 0.6rem;
  z-index: 100;
  font-size: 0.3rem;
  letter-spacing: 0.08rem;
}